////
/// @module line: 
/// @type String
/// @category style
////

// Line
//  Alibaba Base DPL(BASE DPL)
// 

// ************************** ,  *********************** //

/// line-zero
/// @semantic 
/// @export size
/// @group size
/// @type Length
/// @unconfigurable
$line-zero: 0 !default;

/// line-1
/// @semantic 
/// @export size
/// @group size
/// @type Length
$line-1: 1px !default;

/// line-2
/// @semantic 
/// @export size
/// @group size
/// @type Length
$line-2: 2px !default;

/// line-3
/// @semantic 
/// @export size
/// @group size
/// @type Length
$line-3: 3px !default;

/// line-solid
/// @export style
/// @group style
/// @unconfigurable
$line-solid: solid !default;

/// line-dashed
/// @export style
/// @group style
/// @unconfigurable
$line-dashed: dashed !default;

/// line-dotted
/// @export style
/// @group style
/// @unconfigurable
$line-dotted: dotted !default;

// ----------- patch ----------- //
$line-0: $line-zero;
