////
/// @group utils
////

// 
//
// @param {String} $value
//   
//
// @example scss - SCSS 
//   $dimension: strip-units(10px);
//
// @example css - CSS 
//   $dimension: 10;
//
// @return {Number}
@function strip-units($value) {
    @return ($value / ($value * 0 + 1));
}

// CSS
//
// @param {String} $value
// @return {Number}
@function is-length($value) {
    @return type-of($value) != "null" and (str-slice($value + "", 1, 4) == "calc" // calc
    or type-of(index(auto inherit initial 0, $value)) == 'number'
    or (type-of($value) == "number" and not(unitless($value))));
}

// pixelem
//
// @param {Number} $pxval
//   pixel
//
// @param {Number} $base
//   pixel
//
// @example scss - SCSS 
//   .element {
//     font-size: em(12, 14);
//   }
//
// @example css - CSS 
//   .element {
//     font-size: 0.85714em;
//   }
// @require {function} strip-units
// @require {variable} $font-size-body-1
//
// @return {String}

@function em($pxval, $base: $font-size-body-1) {
    @if not unitless($pxval) {
        $pxval: strip-units($pxval);
    }
    @if not unitless($base) {
        $base: strip-units($base);
    }
    @return ($pxval / $base) * 1em;
}

//  1  4 margin, padding, position
//
// @param {List} $shorthand
//
// @example scss - SCSS 
//   .element {
//     margin: unpack(1em 2em);
//     padding: unpack(1em 2em 2em);
//   }
//
// @example css - CSS 
//   .element {
//     margin: 1em 2em 1em 2em;
//     padding: 1em 2em 2em 2em;
//   }
//
// @return {List}
@function unpack($shorthand) {
    @if length($shorthand) == 1 {
        @return nth($shorthand, 1) nth($shorthand, 1) nth($shorthand, 1) nth($shorthand, 1);
    } @else if length($shorthand) == 2 {
        @return nth($shorthand, 1) nth($shorthand, 2) nth($shorthand, 1) nth($shorthand, 2);
    } @else if length($shorthand) == 3 {
        @return nth($shorthand, 1) nth($shorthand, 2) nth($shorthand, 3) nth($shorthand, 2);
    } @else {
        @return $shorthand;
    }
}

// 
//
// @access private
//
// @param {String} $side [$corner-sides-base] - 
// @param {String} $radius [$corner-radius-base] - 
//
// @example scss - SCSS 
//   .element {
//     border-radius: corner-maker();
//   }
//
// @example css - CSS 
//   .element {
//     border-radius: 3px 3px 3px 3px;
//   }
//
// @return {List}

@function corner-maker($side: $corner-sides-base, $radius: 3px) {

    $tl: $radius;
    $tr: $radius;
    $br: $radius;
    $bl: $radius;

    @if ($side ==  top) {
        $br: 0;
        $bl: 0;
    } @else if ($side == right) {
        $tl: 0;
        $bl: 0;
    } @else if ($side == down) {
        $tr: 0;
        $tl: 0;
    } @else if ($side == left) {
        $tr: 0;
        $br: 0;
    }

    @return #{$tl} #{$tr} #{$br} #{$bl};
}

// shadow 
//
//
// @param {String} $direction [$shadow-sides-base] - 
// @param {String} $blur [$shadow-blur-sd1] - 
// @param {Color} $shadow-color [$shadow-color-opacity-sd1] - 
// @param {String} $shadow-x [$shadow-distance-sd1] - x
// @param {String} $shadow-y [$shadow-distance-sd1y] - y
// @param {String} $shadow-spread [$shadow-spread-sd1] - 
//
// @example scss - SCSS 
//   $shadow-1:  shadow-maker();
//
// @return {List}

@function shadow-maker(
    $direction: $shadow-sides-base,
    $blur: $shadow-blur-sd1,
    $shadow-color: $shadow-color-opacity-sd1,
    $shadow-x: $shadow-distance-sd1,
    $shadow-y: $shadow-distance-sd1y,
    $shadow-spread: $shadow-spread-sd1

) {

    $x: $shadow-x;
    $y: $shadow-y;
    $b: $blur;
    $c: $shadow-color;
    $s: $shadow-spread;

    @if ($b == 0) {
        @return 0 0 0 #FFFFFF;
    }

    @if ($direction == $shadow-sides-up) {
        $x: 0;
        $y: -$shadow-y;
    } @else if ($direction == $shadow-sides-right) {
        $y: 0;
    } @else if ($direction == $shadow-sides-down) {
        $x: 0;
    } @else if ($direction == $shadow-sides-left) {
        $x: -$shadow-x;
        $y: 0;
    }

    @return #{$x}px #{$y}px #{$b}px #{$s}px $c;
}
