@import "./_color";
@import "../util/_function";

////
/// @module shadow: 
/// @category style
////

// Elevation & Shadow
//  Alibaba Base DPL(BASE DPL)
// 
// : x == y,  blur , , 
// -------------------------------

// : x, y, color, opacity, elevation
// : base(45), top, right, bottom, left

// ----------------  ---------------- //

/// Sides Base
/// @type String
$shadow-sides-base: "base" !default;
/// Sides Up
/// @type String
$shadow-sides-up: "up" !default;
/// Sides Right
/// @type String
$shadow-sides-right: "right" !default;
/// Sides Down
/// @type String
$shadow-sides-down: "down" !default;
/// Sides Left
/// @type String
$shadow-sides-left: "left" !default;

// ----------------  ----------------- //

/// Distance 1x
/// @type Number
$shadow-distance-sd1: 1;
/// Distance 2x
/// @type Number
$shadow-distance-sd2: 2;
/// Distance 3x
/// @type Number
$shadow-distance-sd3: 3;

/// Distance 1y
/// @type Number
$shadow-distance-sd1y: 1;
/// Distance 2y
/// @type Number
$shadow-distance-sd2y: 2;
/// Distance 3y
/// @type Number
$shadow-distance-sd3y: 3;

// ----------------  ----------------- //

/// Opacity 1
/// @type Number
$shadow-opacity-sd1: .12 !default;
/// Opacity 2
/// @type Number
$shadow-opacity-sd2: .12 !default;
/// Opacity 3
/// @type Number
$shadow-opacity-sd3: .12 !default;

// ----------------  ----------------- //

/// Color 1
/// @type Color
$shadow-color-sd1: $color-black !default;

/// Color 2
/// @type Color
$shadow-color-sd2: $color-black !default;

/// Color 3
/// @type Color
$shadow-color-sd3: $color-black !default;

// 
$shadow-color-opacity-sd1: rgba($shadow-color-sd1, $shadow-opacity-sd1) !default;
$shadow-color-opacity-sd2: rgba($shadow-color-sd2, $shadow-opacity-sd2) !default;
$shadow-color-opacity-sd3: rgba($shadow-color-sd3, $shadow-opacity-sd3) !default;

// ------------------  ---------------- //

/// Blur 1
/// @type Number
$shadow-blur-sd1: 3 !default;

/// Blur 2
/// @type Number
$shadow-blur-sd2: 4 !default;

/// Blur 3
/// @type Number
$shadow-blur-sd3: 5 !default;

// ------------------  ---------------- //

/// Spread 1
/// @type Number
$shadow-spread-sd1: 0 !default;

/// Spread 2
/// @type Number
$shadow-spread-sd2: 0 !default;

/// Spread 3
/// @type Number
$shadow-spread-sd3: 0 !default;

// ------------------ Shadow Values ---------------- //

/// shadow-zero
/// @export
/// @unconfigurable
$shadow-zero: none;

/// shadow-1
/// @semantic 1-
/// @export
/// @unconfigurable
$shadow-1: shadow-maker($shadow-sides-base, $shadow-blur-sd1, $shadow-color-opacity-sd1, $shadow-distance-sd1, $shadow-distance-sd1y, $shadow-spread-sd1);

/// shadow-1-up
/// @semantic 1-
/// @export
/// @unconfigurable
$shadow-1-up: shadow-maker($shadow-sides-up, $shadow-blur-sd1, $shadow-color-opacity-sd1, $shadow-distance-sd1, $shadow-distance-sd1y, $shadow-spread-sd1);

/// shadow-1-right
/// @semantic 1-
/// @export
/// @unconfigurable
$shadow-1-right: shadow-maker($shadow-sides-right, $shadow-blur-sd1, $shadow-color-opacity-sd1, $shadow-distance-sd1, $shadow-distance-sd1y, $shadow-spread-sd1);

/// shadow-1-down
/// @semantic 1-
/// @export
/// @unconfigurable
$shadow-1-down: shadow-maker($shadow-sides-down, $shadow-blur-sd1, $shadow-color-opacity-sd1, $shadow-distance-sd1, $shadow-distance-sd1y, $shadow-spread-sd1);

/// shadow-1-left
/// @semantic 1-
/// @export
/// @unconfigurable
$shadow-1-left: shadow-maker($shadow-sides-left, $shadow-blur-sd1, $shadow-color-opacity-sd1, $shadow-distance-sd1, $shadow-distance-sd1y, $shadow-spread-sd1);

/// shadow-2
/// @semantic 2-
/// @export
/// @unconfigurable
$shadow-2: shadow-maker($shadow-sides-base, $shadow-blur-sd2, $shadow-color-opacity-sd2, $shadow-distance-sd2, $shadow-distance-sd2y, $shadow-spread-sd2);
/// shadow-2-up
/// @semantic 2-
/// @export
/// @unconfigurable
$shadow-2-up: shadow-maker($shadow-sides-up, $shadow-blur-sd2, $shadow-color-opacity-sd2, $shadow-distance-sd2, $shadow-distance-sd2y, $shadow-spread-sd2);
/// shadow-2-right
/// @semantic 2-
/// @export
/// @unconfigurable
$shadow-2-right: shadow-maker($shadow-sides-right, $shadow-blur-sd2, $shadow-color-opacity-sd2, $shadow-distance-sd2, $shadow-distance-sd2y, $shadow-spread-sd2);
/// shadow-2-down
/// @semantic 2-
/// @export
/// @unconfigurable
$shadow-2-down: shadow-maker($shadow-sides-down, $shadow-blur-sd2, $shadow-color-opacity-sd2, $shadow-distance-sd2, $shadow-distance-sd2y, $shadow-spread-sd2);
/// shadow-2-left
/// @semantic 2-
/// @export
/// @unconfigurable
$shadow-2-left: shadow-maker($shadow-sides-left, $shadow-blur-sd2, $shadow-color-opacity-sd2, $shadow-distance-sd2, $shadow-distance-sd2y, $shadow-spread-sd2);

/// shadow-3
/// @semantic 3-
/// @export
/// @unconfigurable
$shadow-3: shadow-maker($shadow-sides-base, $shadow-blur-sd3, $shadow-color-opacity-sd3, $shadow-distance-sd3, $shadow-distance-sd3y, $shadow-spread-sd3);
/// shadow-3-up
/// @semantic 3-
/// @export
/// @unconfigurable
$shadow-3-up: shadow-maker($shadow-sides-up, $shadow-blur-sd3, $shadow-color-opacity-sd3, $shadow-distance-sd3, $shadow-distance-sd3y, $shadow-spread-sd3);
/// shadow-3-right
/// @semantic 3-
/// @export
/// @unconfigurable
$shadow-3-right: shadow-maker($shadow-sides-right, $shadow-blur-sd3, $shadow-color-opacity-sd3, $shadow-distance-sd3, $shadow-distance-sd3y, $shadow-spread-sd3);
/// shadow-3-down
/// @semantic 3-
/// @export
/// @unconfigurable
$shadow-3-down: shadow-maker($shadow-sides-down, $shadow-blur-sd3, $shadow-color-opacity-sd3, $shadow-distance-sd3, $shadow-distance-sd3y, $shadow-spread-sd3);
/// shadow-3-left
/// @semantic 3-
/// @export
/// @unconfigurable
$shadow-3-left: shadow-maker($shadow-sides-left, $shadow-blur-sd3, $shadow-color-opacity-sd3, $shadow-distance-sd3, $shadow-distance-sd3y, $shadow-spread-sd3);

// ----------- patch ----------- //
$shadow-0: $shadow-zero;
