// Corner 
//  Alibaba Base DPL(BASE DPL)
// 
// : x == y,  blur , , 
// : Right Angle(), Rounded()
// -------------------------------

/// corner-1
/// @semantic 
/// @export
/// @type Length
$corner-1: 3px !default;

/// corner-2
/// @semantic 
/// @export
/// @type Length
$corner-2: 6px !default;

/// corner-3
/// @semantic 
/// @export
/// @type Length
$corner-3: 20px !default;

/// corner-circle
/// @semantic 
/// @export
/// @unconfigurable
$corner-circle: 50% !default;

/// corner-semicircle
/// @semantic 
/// @export
/// @unconfigurable
$corner-semicircle: 500px !default;

/// corner-zero
/// @semantic 
/// @export
/// @unconfigurable
$corner-zero: 0 !default;

// ----------- patch ----------- //
$corner-right-angle: $corner-zero;
