////
/// @group utils
////

/// 
///
/// @example scss
///   .wrapper {
///      @include clearfix;
///   }
///
@mixin clearfix {
    &:after {
        visibility: hidden;
        display: block;
        height: 0;
        font-size: 0;
        content: '\0020';
        clear: both;
    }
}



/// ...
///
/// @param {Number | String} $width [100%] - CSScalc
///
/// @example scss - 
///   .element {
///     @include ellipsis;
///   }
///
/// @example css - 
///   .element {
///     display: inline-block;
///     max-width: 100%;
///     overflow: hidden;
///     text-overflow: ellipsis;
///    white-space: nowrap;
///     word-wrap: normal;
///   }
///
@mixin ellipsis($width: 100%) {
    display: inline-block;
    max-width: $width;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
}

/// ...
///
/// @param {Number | String} $line-height [1.2em] - 
/// @param {Number} $line-count [2] - , 
/// @param {Color} $bg-color [#fff] - 
///
/// @example scss - 
///   .element {
///     @include multi-line-ellipsis(1.2em, 3, #fff);
///   }
///
/// @example css - 
///   .element {
///     overflow: hidden;
///     position: relative;
///     line-height: 1.2em;
///     max-height: 3.6em;
///     text-align: justify;
///     padding-right: 0em;
///   }
///
///   .element:before {
///     content: '...';
///     position: absolute;
///     right: 0;
///     bottom: 0;
///     background: #fff;
///   }
///
///   .element:after {
///     content: '';
///     position: absolute;
///     right: 0;
///     width: 1em;
///     height: 1em;
///     margin-top: 0.2em;
///     background: #fff;
///   }
///
@mixin multi-line-ellipsis(
    $line-height: 1.2em,
    $line-count: 2,
    $bg-color: #fff
) {
    overflow: hidden;
    position: relative;
    line-height: $line-height;
    max-height: $line-height * $line-count;
    text-align: justify;
    padding-right: 0;

    &:before {
        content: '...';
        position: absolute;
        right: 0;
        bottom: 0;
        background: $bg-color;
    }

    &:after {
        content: '';
        position: absolute;
        right: 0;
        width: 1em;
        height: 1em;
        margin-top: .2em;
        background: $bg-color;
    }
}

/// 
///
/// text-indent: -9999pxHack 
///
/// @link http://nicolasgallagher.com/another-css-image-replacement-technique
///
/// @example scss
///   .element {
///      @include hide-text;
///      background: url(logo.png);
///   }
///
@mixin hide-text {
    font: 0/0 a;
    text-shadow: none;
    color: transparent;
}

/// () (transform )
///
/// @param {String} $inner-selector ['.inner'] - 
/// @param {Bool} $horizontal [true] - 
///
/// @example scss - 
///   .element {
///      @include center-tl;
///   }
///
/// @example css - 
///   .element {
///       position: relative;
///   }
///
///   .element > .inner {
///       position: absolute;
///       top: 50%;
///       left: 50%;
///       transform: translate(-50%, -50%);
///   }
///
@mixin center-tl(
    $inner-selector: '.inner',
    $horizontal: true
) {

    position: relative;

    & > #{$inner-selector} {
        position: absolute;
        top: 50%;
        $translate-val: translateY(-50%);

        @if $horizontal {
            left: 50%;
            $translate-val: translate(-50%, -50%);
        }

        transform: $translate-val;
    }
}

/// () (table )
///
/// @param {String} $inner-selector ['.inner'] - 
/// @param {Bool} $horizontal [true] - 
///
/// @example scss - 
///   .element {
///     @include center-td;
///   }
///
/// @example css - 
///   .element {
///       text-align: center;
///       display: table;
///   }
///
///   .element > .inner {
///       display: table-cell;
///       vertical-align: middle;
///   }
///
@mixin center-td(
    $inner-selector: '.inner',
    $horizontal: true
) {

    @if $horizontal {
        text-align: center;
    }

    display: table;

    & > #{$inner-selector} {
        display: table-cell;
        vertical-align: middle;
    }
}

/// width,height
///
/// @param {String | List} $size - CSS
///
/// @example scss - 
///   .element {
///     @include size(2em 4em);
///   }
///
/// @example css - 
///   .element {
///     width: 2em;
///     height: 4em;
///   }
///
@mixin size($size) {

    @if length($size) == 1 {
        width: $size;
        height: $size;
    } @else if length($size) == 2 {
        width: nth($size, 1);
        height: nth($size, 2);
    }
}

// postion (null)
//
// @param {String} $position [relative]
//   CSS position
//
// @param {List} $coordinates [null null null null]
//    1 ~ 4
//
// @example scss - 
//   .element {
//     @include position(absolute, 0 null null 10px);
//   }
//
// @example css - 
//   .element {
//     position: absolute;
//     left: 10px;
//     top: 0;
//   }
//
// @require {function} is-length
// @require {function} unpack
@mixin position($position: relative, $coordinates: null null null null) {

    // 
    @if type-of($position) == list {
        $coordinates: $position;
        $position: relative;
    }

    $coordinates: unpack($coordinates);

    $offsets: (
        top: nth($coordinates, 1),
        right: nth($coordinates, 2),
        bottom: nth($coordinates, 3),
        left: nth($coordinates, 4)
    );

    position: $position;

    @each $offset, $value in $offsets {

        // 
        @if is-length($value) {
            #{$offset}: $value;
        }
    }
}

// (8)
//
// @param {Number | List} $size
//   
//   2
//
// @param {String | List} $color
//   2
//
// @param {String} $direction
//   up | down | right | left | up-right | up-left | down-right | down-left | inset-up | inset-down | inset-left | inset-right
//
// @example scss - 
//   .element {
//     &:before {
//       content: " ";
//       @include triangle(100px 200px, blue, up);
//     }
//   }
//
// @example css - 
//   .example:before {
//     content: " ";
//     height: 0;
//     width: 0;
//     border-bottom: 200px solid blue;
//     border-left: 50px solid transparent;
//     border-right: 50px solid transparent;
//   }
//
@mixin triangle($size, $color, $direction) {
    $width: nth($size, 1);
    $height: nth($size, length($size));
    $foreground-color: nth($color, 1);
    $background-color: if(length($color) == 2, nth($color, 2), transparent);
    height: 0;
    width: 0;

    @if ($direction == up) or ($direction == down) or ($direction == right) or ($direction == left) {
        $width: $width / 2;
        $height: if(length($size) > 1, $height, $height/2);

        @if $direction == up {
            border-bottom: $height solid $foreground-color;
            border-left: $width solid $background-color;
            border-right: $width solid $background-color;
        } @else if $direction == right {
            border-bottom: $width solid $background-color;
            border-left: $height solid $foreground-color;
            border-top: $width solid $background-color;
        } @else if $direction == down {
            border-left: $width solid $background-color;
            border-right: $width solid $background-color;
            border-top: $height solid $foreground-color;
        } @else if $direction == left {
            border-bottom: $width solid $background-color;
            border-right: $height solid $foreground-color;
            border-top: $width solid $background-color;
        }
    } @else if ($direction == up-right) or ($direction == up-left) {
        border-top: $height solid $foreground-color;

        @if $direction == up-right {
            border-left: $width solid $background-color;
        } @else if $direction == up-left {
            border-right: $width solid $background-color;
        }
    } @else if ($direction == down-right) or ($direction == down-left) {
        border-bottom: $height solid $foreground-color;

        @if $direction == down-right {
            border-left: $width solid $background-color;
        } @else if $direction == down-left {
            border-right: $width solid $background-color;
        }
    } @else if ($direction == inset-up) {
        border-color: $background-color $background-color $foreground-color;
        border-style: solid;
        border-width: $height $width;
    } @else if ($direction == inset-down) {
        border-color: $foreground-color $background-color $background-color;
        border-style: solid;
        border-width: $height $width;
    } @else if ($direction == inset-right) {
        border-color: $background-color $background-color $background-color $foreground-color;
        border-style: solid;
        border-width: $width $height;
    } @else if ($direction == inset-left) {
        border-color: $background-color $foreground-color $background-color $background-color;
        border-style: solid;
        border-width: $width $height;
    }
}

@mixin box-sizing {
    box-sizing: border-box;
    *,
    *:before,
    *:after {
        box-sizing: border-box;
    }
}

@mixin icon-size($size, $marginLeft: false, $marginRight: false, $transform: false) {
    @if ($transform) {
        transform: $transform;
    }
    @if ($marginLeft) {
        margin-left: $marginLeft;
    }
    @if ($marginRight) {
        margin-right: $marginRight;
    }

    &:before,
    & .#{$css-prefix}icon-remote {
        width: $size;
        font-size: $size;
        line-height: inherit;
    }

    // Chrome12px
    @if (type-of($size) == 'number') {
        @if ($size < 12) {
            @media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: .001dpcm) {
                @if ($transform) {
                    transform: scale($size / $icon-s) $transform;
                } @else {
                    transform: scale($size / $icon-s);
                }
                @if ($marginLeft) {
                    margin-left: $marginLeft - ($icon-s - $size) / 2;
                } @else {
                    margin-left: -($icon-s - $size) / 2;
                }
                @if ($marginRight) {
                    margin-right: $marginRight - ($icon-s - $size) / 2;
                } @else {
                    margin-right: -($icon-s - $size) / 2;
                }

                &:before {
                    width: $icon-s;
                    font-size: $icon-s;
                }
            }
        }
    }
}

@mixin icon-square-size($size, $marginTop: false, $marginRight: false, $marginBottom: false, $marginLeft: false, $transform: false) {
    @if ($transform) {
        transform: $transform;
    }
    @if ($marginTop) {
        margin-top: $marginTop;
    }
    @if ($marginRight) {
        margin-right: $marginRight;
    }
    @if ($marginBottom) {
        margin-bottom: $marginBottom;
    }
    @if ($marginLeft) {
        margin-left: $marginLeft;
    }

    width: $size;
    height: $size;
    line-height: $size;

    &:before {
        width: $size;
        height: $size;
        font-size: $size;
        line-height: $size;
    }

    // Chrome12px
    @if ($size < 12) {
        @media all and (-webkit-min-device-pixel-ratio: 0) and (min-resolution: .001dpcm) {
            @if ($transform) {
                transform: scale($size / $icon-s) $transform;
            } @else {
                transform: scale($size / $icon-s);
            }
            @if ($marginTop) {
                margin-top: $marginTop - ($icon-s - $size) / 2;
            } @else {
                margin-top: -($icon-s - $size) / 2;
            }
            @if ($marginRight) {
                margin-right: $marginRight - ($icon-s - $size) / 2;
            } @else {
                margin-right: -($icon-s - $size) / 2;
            }
            @if ($marginBottom) {
                margin-bottom: $marginBottom - ($icon-s - $size) / 2;
            } @else {
                margin-bottom: -($icon-s - $size) / 2;
            }
            @if ($marginLeft) {
                margin-left: $marginLeft - ($icon-s - $size) / 2;
            } @else {
                margin-left: -($icon-s - $size) / 2;
            }

            width: $icon-s;
            height: $icon-s;
            line-height: $icon-s;

            &:before {
                width: $icon-s;
                height: $icon-s;
                font-size: $icon-s;
                line-height: $icon-s;
            }
        }
    }
}

@mixin font-face-handler($name, $url) {
    @if ($name != Roboto) and ($name != "") {
        @font-face {
            font-family: "#{$name}";
            src: url("#{$url}");
        }
    }
}

///  button 
///
/// @param {String} $color 
/// @param {String} $color-hover 
/// @param {String} $bg-color 
/// @param {String} $bg-color-hover 
/// @param {String} $border-color 
/// @param {String} $border-color-hover 
@mixin button-color($color, $color-hover, $bg-color: transparent, $bg-color-hover: transparent, $border-color: transparent, $border-color-hover: transparent) {
    background-color: $bg-color;
    border-color: $border-color;

    &,
    &:link,
    &:visited,
    &.visited {
        color: $color;
    }

    &:focus,
    &:hover,
    &.hover,
    &:active,
    &.active {
        color: $color-hover;
        background-color: $bg-color-hover;
        border-color: $border-color-hover;
        text-decoration: none;
    }
}

///  button 
///
/// @param {Number} $padding 
/// @param {Number} $height 
/// @param {Number} $font-size 
/// @param {Number} $border-width 
@mixin button-size($padding, $height, $font-size, $border-width) {
    padding: 0 $padding;
    height: $height;
    font-size: $font-size;
    border-width: $border-width;
}
